.RouletteFortune {
  display: flex;
  flex-direction: column;
  justify-content: center;


  .roulette-container {
    display: grid;
    justify-content: center;
    padding: 5px;
text-align: center;
  }

  canvas {
    display: block;
    width: 300px;
    height: 300px;

    border-style: solid;
    border-radius: 200px;
    transition: border ease 3s;

  }
}

.casino__buttonsDiv {
  display: flex;
  justify-content: center;

}

.casino__buttons {
  width: 80px;
  height: 80px;
  outline: none;
  border-radius: 12px;
  margin-right: 3px;
  border: white;
  border-style: solid;
}


.casino__button_active {
  width: 80px;
  height: 80px;
  outline: none;
  border-radius: 12px;
  margin-right: 3px;
  border: orange;
  border-style: solid;
}